import {
	Grid,
	Table,
	TableBody,
	TableContainer,
	Alert,
	useTheme,
	InputLabel,
	Button,
	Box,
	CardMedia,
	Fab,
	Stack,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { useQuery, gql } from '@apollo/client';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/styles';
import ImageUploading, { ImageListType } from 'react-images-uploading';
import { useEffect, useState } from 'react';
import clsx from 'clsx';
import AnimateButton from '../../../ui-component/extended/AnimateButton';

import SubCard from '../../../ui-component/cards/SubCard';
import { gridSpacing } from '../../../store/constant';
import { TProperty } from '../../../types';
import { getProperty } from '../../../graphql/queries';
import Loader from '../../../ui-component/Loader';
import TextRow from '../components/TextRow';

const useStyles = makeStyles(() => ({
	fileInput: {
		background: '#fcfcfe',
		width: '100%',
		marginBottom: '24px',
		'&:dragover, &:dragenter': {
			background: '#333',
		},
	},
	fileContainer: {
		padding: '30px 0',
		textAlign: 'center',
		borderRadius: '4px',
		cursor: 'pointer',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
	},
	uploadImage: {
		position: 'relative',
		overflow: 'hidden',
		borderRadius: '4px',
		cursor: 'pointer',
		width: '100%',
		minHeight: '100px',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
		'&:hover $imageHover': {
			opacity: 1,
			zIndex: 1,
		},
	},
	imageHover: {
		position: 'absolute',
		opacity: 0,
		zIndex: -1,
		left: '0',
		top: '0',
		background: 'rgba(255, 255, 255, .8)',
		width: '100%',
		height: '100%',
		display: 'inline-flex',
		transition: 'all 0.3s ease',
	},
	removeButton: {
		margin: 'auto',
	},
}));

type Props = {
	id: string;
};

const PropertyEdit = ({ id }: Props) => {
	const { loading, error, data } = useQuery(gql(getProperty), {
		variables: { PropertyId: id },
		errorPolicy: 'all',
	});
	const property = _get(data, 'getProperty', {});
	const propertyThumbnail = _get(property, 'Thumbnail', null);
	const [images, setImages] = useState<Array<{ dataURL: string; file: any }>>(
		[]
	);
	const theme = useTheme();
	const classes = useStyles();

	useEffect(() => {
		if (!propertyThumbnail) return;

		const { presignedUrl = '' } = propertyThumbnail;
		const thumbnail = {
			dataURL: presignedUrl,
			file: null,
		};
		setImages([thumbnail]);
	}, [propertyThumbnail]);

	if (loading) return <Loader />;

	if (error && _isEmpty(property)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Property not found
			</Alert>
		);
	}

	const onChange = (imageList: ImageListType) => {
		setImages(imageList as never[]);
	};

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item lg={6} xs={12}>
				<SubCard title='Property Information'>
					<TableContainer>
						<Table
							sx={{
								'& td': {
									borderBottom: 'none',
								},
							}}
							size='small'
						>
							<TableBody>
								<TextRow<TProperty> data={property} source='PropertyNo' label='Property No' />
								<TextRow<TProperty>
									data={property}
									source='PropertyData.StreetAddress'
									label='Street Address'
								/>
								<TextRow<TProperty> data={property} source='PropertyData.County' label='County' />
								<TextRow<TProperty> data={property} source='PropertyData.State' label='State' />
								<TextRow<TProperty> data={property} source='PropertyData.Zip' label='Zip' />
								<TextRow<TProperty> data={property} source='PropertyData.City' label='City' />
							</TableBody>
						</Table>
					</TableContainer>
				</SubCard>
			</Grid>
			<Grid item lg={6} xs={12}>
				<SubCard title='Property Photo'>
					<ImageUploading value={images} onChange={onChange}>
						{({ onImageUpload, onImageRemove, dragProps }) => (
							<Box component='div'>
								<Button
									id='file-upload'
									type='button'
									className={clsx(classes.fileInput)}
									onClick={onImageUpload}
									{...dragProps}
								>
									<InputLabel className={classes.fileContainer}>
										<CloudUploadIcon />
										Drop file here to upload
									</InputLabel>
								</Button>
								<Grid container spacing={1}>
									{images.map((image, index) => (
										<Grid
											item
											xs={12}
											md={6}
											lg={4}
											alignContent='center'
											alignItems='center'
											// eslint-disable-next-line react/no-array-index-key
											key={index}
										>
											<div className={classes.uploadImage}>
												<CardMedia component='img' image={image.dataURL} title='image' />
												<div className={classes.imageHover}>
													<Fab
														color='secondary'
														size='small'
														className={classes.removeButton}
														onClick={() => onImageRemove(index)}
													>
														<CloseIcon />
													</Fab>
												</div>
											</div>
										</Grid>
									))}
								</Grid>
							</Box>
						)}
					</ImageUploading>
				</SubCard>
			</Grid>
			<Grid item xs={12}>
				<Stack direction='row' justifyContent='flex-end'>
					<AnimateButton>
						<Button
							color='primary'
							variant='contained'
							type='submit'
						>
							Update
						</Button>
					</AnimateButton>
				</Stack>
			</Grid>
		</Grid>
	);
};

export default PropertyEdit;
